function _1(md){return(
md`# 🤔 How to: Embed a Notebook in a React App

Squinting at them from a certain distance, Observable notebooks and [React apps](https://reactjs.org/tutorial/tutorial.html) can look like close cousins: both are functional, reactive ways of writing UIs in JavaScript.

Let’s see if we can get them to play nicely together, by embedding and controlling a notebook from React.

If you haven’t already read the [introduction to downloading and embedding notebooks](/@observablehq/downloading-and-embedding-notebooks), read that first. No worries, I can wait.

Now that we’re on the same page, let’s start with a small, pretty animation — particles drifting on a random walk, connected by faint lines when they approach one another.`
)}

function* _animation(DOM,width,$0)
{
  const height = 300;
  const min = 0;
  const max = 100;
  const max2 = Math.pow(max, 2);
  const context = DOM.context2d(width, height);
  const canvas = context.canvas;

  const n = 100;
  const particles = new Array(n);
  for (let i = 0; i < n; ++i) {
    particles[i] = {
      x: (Math.random() * (width + max * 2)) - max,
      y: (Math.random() * (height + max * 2)) - max,
      vx: 0,
      vy: 0
    };
  }

  while (true) {
    for (let i = 0; i < n; ++i) {
      const p = particles[i];
      p.x += p.vx;
      if (p.x < -max) p.x += width + max;
      else if (p.x > width + max) p.x -= width + max;
      p.y += p.vy;
      if (p.y < -max) p.y += height + max;
      else if (p.y > height + max) p.y -= height + max;
      p.vx += $0.value * (Math.random() - .5) - 0.01 * p.vx;
      p.vy += $0.value * (Math.random() - .5) - 0.01 * p.vy;
    }

    for (let i = 0; i < n; ++i) {
      for (let j = i + 1; j < n; ++j) {
        const pi = particles[i];
        const pj = particles[j];
        const dist = Math.pow(pi.x - pj.x, 2) + Math.pow(pi.y - pj.y, 2);
        if (dist < max2) {
          context.beginPath();
          context.strokeStyle = `hsl(0,0%,${100 - (((max2 - dist) / max2) * 25)}%)`;
          context.moveTo(pi.x, pi.y);
          context.lineTo(pj.x, pj.y);
          context.stroke();
        }
      }
    }

    yield canvas;
  }
}


