async function _8(md,FileAttachment){return(
md`Now we’re ready to edit the generated \`src/App.js\`, importing the Runtime and the notebook, loading it up, and embedding the animation in the page. We’ll use a [React ref](https://reactjs.org/docs/refs-and-the-dom.html) to provide the Observable runtime with a reference to the React DOM that we can render into.

\`\`\`js
import React, {Component} from "react";
import {Runtime, Inspector} from "@observablehq/runtime";
import notebook from "@observablehq/how-to-embed-a-notebook-in-a-react-app";

class App extends Component {
  animationRef = React.createRef();

  componentDidMount() {
    const runtime = new Runtime();
    runtime.module(notebook, name => {
      if (name === "animation") {
        return new Inspector(this.animationRef.current);
      }
    });
  }

  render() {
    return (
      <div className="App">
        <div ref={this.animationRef}></div>
      </div>
    );
  }
}

export default App;
\`\`\`

Voilà. Your browser should already have refreshed:

<img src="${await FileAttachment("image@2.png").url()}" style="max-width: 500px;" />
`
)}

