function _11(md){return(
md`Fiddling with the input, you’ll notice that changes to \`state.speed\` aren’t affecting the animation ... yet. We have React state changes, but they aren't yet connected to the Observable runtime.

Let’s fix that. In \`componentDidMount\`, we’ll store a reference this notebook’s \`mutable speed\` variable, and then use React’s \`componentDidUpdate\` callback to set that variable’s value.`
)}

function _12(highlight){return(
highlight(
  [11, 12, 13, 14, 15, 19, 20, 21, 22, 23],
  `class App extends Component {
  this.state = {speed: 0.1};
  this.animationRef = React.createRef();

  componentDidMount() {
    const runtime = new Runtime();
    runtime.module(notebook, name => {
      if (name === "animation") {
        return new Inspector(this.animationRef.current);
      }
      if (name === "mutable speed") {
        return {fulfilled: (value) => {
          this.animationSpeed = value;
        }};
      }
    });
  }

  componentDidUpdate(nextProps, nextState) {
    if (nextState.speed !== this.state.speed) {
      this.animationSpeed.value = nextState.speed;
    }
  }

  setSpeed = (event) => {
    this.setState({speed: event.target.valueAsNumber});
  }

  render() {
    return (
      <div className="App">
        <div ref={this.animationRef}></div>
        <small>Speed: {this.state.speed}</small>
        <br />
        <input type="range" min="0" max="2" step="0.1"
          value={this.state.speed} onChange={this.setSpeed} />
      </div>
    );
  }
}`
)
)}

