function _highlight(hljs,html){return(
function highlight(lines, code) {
  let pretty = hljs.highlightAuto(code, ["js"]).value;
  pretty = pretty.split(/\r?\n/).map((line, i) => {
    return lines.indexOf(i + 1) > -1 ? 
      `<span class="highlight">${line}</span>` :
      line;
  }).join('\n');
  return html`<pre><code class="js hljs javascript">${pretty}</code></pre>`;
}
)}

function _hljs(require){return(
require("https://bundle.run/highlight.js@9.12.0")
)}

