// https://observablehq.com/@d3/realtime-horizon-chart@703
function _1(md){return(
md`# Realtime Horizon Chart

This [horizon chart](/@d3/horizon-chart) shows realtime [random walks](https://en.wikipedia.org/wiki/Random_walk). Only the new values are drawn on each tick, while the old values are shifted to the left.`
)}

function _scheme(html){return(
html`<select>
<option value="schemeBlues">Blues</option>
<option value="schemeGreens" selected>Greens</option>
<option value="schemeGreys">Greys</option>
<option value="schemeOranges">Oranges</option>
<option value="schemePurples">Purples</option>
<option value="schemeReds">Reds</option>
<option value="schemeBuGn">BuGn</option>
<option value="schemeBuPu">BuPu</option>
<option value="schemeGnBu">GnBu</option>
<option value="schemeOrRd">OrRd</option>
<option value="schemePuBu">PuBu</option>
<option value="schemePuBuGn">PuBuGn</option>
<option value="schemePuRd">PuRd</option>
<option value="schemeRdPu">RdPu</option>
<option value="schemeYlGn">YlGn</option>
<option value="schemeYlGnBu">YlGnBu</option>
<option value="schemeYlOrBr">YlOrBr</option>
<option value="schemeYlOrRd">YlOrRd</option>
</select>`
)}

function _overlap(html,d3)
{
  const form = html`<form>
  <input name=i type=range min=1 max=${d3.schemeBlues.length - 1} value=7 step=1 style="width:180px;">
  <output style="font-size:smaller;font-style:oblique;" name=o></output>
</form>`;
  form.i.oninput = () => form.o.value = `${form.value = form.i.valueAsNumber} band${form.i.valueAsNumber === 1 ? "" : "s"}`;
  form.i.oninput();
  return form;
}


function _chart(html,d3,data,DOM,width,step,margin,height,overlap,color,y,xAxis)
{
  const div = html`<div style="position:relative;">`;

  const canvas = d3.select(div)
    .selectAll("canvas")
    .data(data)
    .enter().append(() => DOM.context2d(width, step, 1).canvas)
      .style("position", "absolute")
      .style("image-rendering", "pixelated")
      .style("top", (d, i) => `${i * (step + 1) + margin.top}px`)
      .property("context", function() { return this.getContext("2d"); })
      .each(horizon);

  const svg = d3.select(div.appendChild(DOM.svg(width, height)))
      .style("position", "relative")
      .style("font", "10px sans-serif");

  const gX = svg.append("g");

  svg.append("g")
    .selectAll("text")
    .data(data)
    .join("text")
      .attr("x", 4)
      .attr("y", (d, i) => (i + 0.5) * (step + 1) + margin.top)
      .attr("dy", "0.35em")
      .text((d, i) => i);

  const rule = svg.append("line")
      .attr("stroke", "#000")
      .attr("y1", margin.top - 6)
      .attr("y2", height - margin.bottom - 1)
      .attr("x1", 0.5)
      .attr("x2", 0.5);

  svg.on("mousemove touchmove", (event) => {
    const x = d3.pointer(event, svg.node())[0] + 0.5;
    rule.attr("x1", x).attr("x2", x);
  });

  function horizon(d) {
    const {context} = this;
    const {length: k} = d;
    if (k < width) context.drawImage(this, k, 0, width - k, step, 0, 0, width - k, step);
    context.fillStyle = "#fff";
    context.fillRect(width - k, 0, k, step);
    for (let i = 0; i < overlap; ++i) {
      context.save();
      context.translate(width - k, (i + 1) * step);
      context.fillStyle = color(i);
      for (let j = 0; j < k; ++j) {
        context.fillRect(j, y(d[j]), 1, -y(d[j]));
      }
      context.restore();
    }
  }

  div.update = data => {
    canvas.data(data).each(horizon);
    gX.call(xAxis);
  };

  return div;
}


function _height(data,step,margin){return(
data.length * (step + 1) + margin.top + margin.bottom
)}

function _margin(){return(
{top: 30, right: 10, bottom: 0, left: 10}
)}

function _step(){return(
29
)}

function _color(d3,scheme,overlap){return(
i => d3[scheme][Math.max(3, overlap)][i + Math.max(0, 3 - overlap)]
)}

function _x(d3,width){return(
d3.scaleTime()
    .range([0, width])
)}

function _y(d3,overlap,step){return(
d3.scaleLinear()
    .rangeRound([0, -overlap * step])
)}

function _xAxis(margin,d3,x,width){return(
g => g
    .attr("transform", `translate(0,${margin.top})`)
    .call(d3.axisTop(x).ticks(width / 80).tickSizeOuter(0))
    .call(g => g.selectAll(".tick").filter(d => x(d) < margin.left || x(d) >= width - margin.right).remove())
    .call(g => g.select(".domain").remove())
)}

function _data(walk)
{
  const n = 20, m = 964;
  const data = new Array(n);
  for (let i = 0; i < n; ++i) {
    const d = data[i] = new Float64Array(m);
    for (let j = 0, v = 0; j < m; ++j) {
      d[j] = v = walk(v);
    }
  }
  return data;
}


function* _update(data,Promises,walk,x,width,chart)
{
  const period = 250, m = data[0].length;
  const tail = data.map(d => d.subarray(m - 1, m));
  while (true) {
    const then = new Date(Math.ceil((Date.now() + 1) / period) * period);
    yield Promises.when(then, then);
    for (const d of data) d.copyWithin(0, 1, m), d[m - 1] = walk(d[m - 1]);
    x.domain([then - period * width, then]);
    chart.update(tail);
  }
}


function _walk(){return(
function walk(v) {
  return Math.max(0, Math.min(1, v + (Math.random() - 0.5) * 0.05));
}
)}

function _d3(require){return(
require("d3@6")
)}

export default function define(runtime, observer) {
  const main = runtime.module();
  main.variable(observer()).define(["md"], _1);
  main.variable(observer("viewof scheme")).define("viewof scheme", ["html"], _scheme);
  main.variable(observer("scheme")).define("scheme", ["Generators", "viewof scheme"], (G, _) => G.input(_));
  main.variable(observer("viewof overlap")).define("viewof overlap", ["html","d3"], _overlap);
  main.variable(observer("overlap")).define("overlap", ["Generators", "viewof overlap"], (G, _) => G.input(_));
  main.variable(observer("chart")).define("chart", ["html","d3","data","DOM","width","step","margin","height","overlap","color","y","xAxis"], _chart);
  main.variable(observer("height")).define("height", ["data","step","margin"], _height);
  main.variable(observer("margin")).define("margin", _margin);
  main.variable(observer("step")).define("step", _step);
  main.variable(observer("color")).define("color", ["d3","scheme","overlap"], _color);
  main.variable(observer("x")).define("x", ["d3","width"], _x);
  main.variable(observer("y")).define("y", ["d3","overlap","step"], _y);
  main.variable(observer("xAxis")).define("xAxis", ["margin","d3","x","width"], _xAxis);
  main.variable(observer("data")).define("data", ["walk"], _data);
  main.variable(observer("update")).define("update", ["data","Promises","walk","x","width","chart"], _update);
  main.variable(observer("walk")).define("walk", _walk);
  main.variable(observer("d3")).define("d3", ["require"], _d3);
  return main;
}
