// https://observablehq.com/@mbostock/psr-b1919-21@111
function _1(md){return(
md`# PSR B1919+21

Data: [Borgar Þorsteinsson](https://bl.ocks.org/borgar/31c1e476b8e92a11d7e9), [Michael Zöllner](http://i.document.m05.de/2013/05/23/joy-divisions-unknown-pleasures-printed-in-3d/)`
)}

function _chart(d3,width,height,data,y,area,line,xAxis)
{
  const svg = d3.create("svg")
      .attr("viewBox", [0, 0, width, height]);

  const serie = svg.append("g")
    .selectAll("g")
    .data(data)
    .join("g")
      .attr("transform", (d, i) => `translate(0,${y(i) + 1})`);
  
  serie.append("path")
      .attr("fill", "#fff")
      .attr("d", area);
  
  serie.append("path")
      .attr("fill", "none")
      .attr("stroke", "black")
      .attr("d", line);

  svg.append("g")
      .call(xAxis);
  
  return svg.node();
}


function _overlap(){return(
16
)}

function _height(){return(
720
)}

function _margin(){return(
{top: 60, right: 10, bottom: 20, left: 10}
)}

function _x(d3,data,margin,width){return(
d3.scaleLinear()
    .domain([0, data[0].length - 1])
    .range([margin.left, width - margin.right])
)}

function _y(d3,data,margin,height){return(
d3.scalePoint()
    .domain(data.map((d, i) => i))
    .range([margin.top, height - margin.bottom])
)}

function _z(d3,data,overlap,y){return(
d3.scaleLinear()
    .domain([
      d3.min(data, d => d3.min(d)),
      d3.max(data, d => d3.max(d))
    ])
    .range([0, -overlap * y.step()])
)}

function _xAxis(height,margin,d3,x,width){return(
g => g
  .attr("transform", `translate(0,${height - margin.bottom})`)
  .call(d3.axisBottom(x.copy().domain([0, 92])).ticks(width / 80))
  .call(g => g.select(".domain").remove())
  .call(g => g.select(".tick:first-of-type text").append("tspan").attr("x", 10).text(" ms"))
)}

function _area(d3,x,z){return(
d3.area()
    .defined(d => !isNaN(d))
    .x((d, i) => x(i))
    .y0(0)
    .y1(z)
)}

function _line(area){return(
area.lineY1()
)}

function _data(FileAttachment){return(
FileAttachment("pulsar.csv").csv({typed: true, array: true})
)}

export default function define(runtime, observer) {
  const main = runtime.module();
  function toString() { return this.url; }
  const fileAttachments = new Map([
    ["pulsar.csv", {url: new URL("./files/d8bb57e2ce25a2c277c80011c3aa3c6ad6c31df240f54193509f46623bc6612f19299403aba9778f5d3ce79285df9f615ac323906b2b1de2117e20f63af62f6b.csv", import.meta.url), mimeType: "text/csv", toString}]
  ]);
  main.builtin("FileAttachment", runtime.fileAttachments(name => fileAttachments.get(name)));
  main.variable(observer()).define(["md"], _1);
  main.variable(observer("chart")).define("chart", ["d3","width","height","data","y","area","line","xAxis"], _chart);
  main.variable(observer("overlap")).define("overlap", _overlap);
  main.variable(observer("height")).define("height", _height);
  main.variable(observer("margin")).define("margin", _margin);
  main.variable(observer("x")).define("x", ["d3","data","margin","width"], _x);
  main.variable(observer("y")).define("y", ["d3","data","margin","height"], _y);
  main.variable(observer("z")).define("z", ["d3","data","overlap","y"], _z);
  main.variable(observer("xAxis")).define("xAxis", ["height","margin","d3","x","width"], _xAxis);
  main.variable(observer("area")).define("area", ["d3","x","z"], _area);
  main.variable(observer("line")).define("line", ["area"], _line);
  main.variable(observer("data")).define("data", ["FileAttachment"], _data);
  return main;
}
